import Axios from "axios";
import React, { Component } from "react";
import BootstrapTable from "react-bootstrap-table-next";
import paginationFactory from "react-bootstrap-table2-paginator";
import ToolkitProvider, { Search } from "react-bootstrap-table2-toolkit";
import Skeleton from "react-loading-skeleton";
import { connect } from "react-redux";
import { reduxForm, reset } from "redux-form";
import { getUUID } from "../actions/fakturAction";
import { berhasil, gagal } from "../Alert";
import notrespond from "../Images/500.svg";
import FormSaldoAwalStok from "./Component/FormSaldoAwalStok";
import { getListDataSaldoAwalStok } from "../actions/transaksuPenjualanLMAction";
import Loader from "react-loader-spinner";
import { Link } from "react-router-dom";
import HeaderCard from "../HeaderCard";

const { SearchBar } = Search;
const server = process.env.REACT_APP_BACKEND_URL_NON_MASTER;
const serverMaster = process.env.REACT_APP_BACKEND_URL;
const maptostate = (state) => {
  return {
    noFaktur: state.provinsi.noFaktur,
    username: state.provinsi.username,
    dataSaldoAwalStok: state.provinsi.dataSaldoAwalStok,
  };
};
class SaldoAwalStok extends Component {
  constructor(props) {
    super(props);
    this.state = {
      tipeTransksi: "HUTANG",
      listSales: [],
      columns: [
        {
          dataField: "noFaktur",
          text: "Nomor Faktur",
        },
        {
          dataField: "footerItem.kadar",
          text: "Kadar",
        },
        {
          dataField: "footerItem.totalNetto",
          text: "Netto",
        },
      ],
      tanggal: "",
      sales: "",
      loading: false,
    };
  }
  sendSaldoAwal() {
    if (this.state.tanggal === "") {
      gagal("Mohon isi tanggal");
      return false;
    } else if (this.state.sales === "") {
      gagal("Mohon isi sales");
      return false;
    } else if (localStorage.getItem("SaldoAwalStok") === null) {
      gagal("Data masih Kosong, Silahkan tambahkan dahulu");
      return false;
    }
    this.setState({
      loading: true,
    });
    Axios.post(
      server + "cst/dc/pjl",
      JSON.parse(localStorage.getItem("SaldoAwalStok"))
    )
      .then(() =>
        this.setState({
          loading: false,
        })
      )
      .then(() => berhasil("Saldo Awal Berhasil Disimpan"))
      .then(() => localStorage.removeItem("noFaktur"))
      .then(() => this.props.dispatch(getUUID()))
      .catch(() =>
        gagal(
          "Sepertinya ada gangguan, Mohon periksa koneksi anda",
          this.setState({ loading: false })
        )
      );
  }

  setTipe(e) {
    this.setState({
      tipeTransksi: e.target.value,
    });
    this.props.dispatch(reset("FormSaldoAwalPiutang"));
  }

  componentDidMount() {
    this.props.dispatch(getUUID());
    this.props.dispatch(getListDataSaldoAwalStok());
    Axios.get(serverMaster + "storages2?tipeData=LOKASI")
      .then((res) =>
        this.setState({
          listSales: res.data,
        })
      )
      .catch(() => gagal("Mohon periksa koneksi anda"));
  }
  handleChange = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
  };

  handlerSimpan(data) {
    if (this.state.tanggal === "") {
      gagal("Mohon isi tanggal");
      return false;
    } else if (this.state.sales === "") {
      gagal("Mohon isi sales");
      return false;
    }
    var isi = {
      kodeLokasi: "-",
      namaToko: "-",
      kodeSales: this.state.sales,
      kodeCustomer: "-",
      noBon: this.props.noFaktur,
      noFaktur: this.props.noFaktur,
      tanggal: this.state.tanggal,
      tipeTransaksi: "SALDO-AWAL-PIUTANG",
      footerDocument: {
        inputBy: this.props.username, //INPUT BY DEFAULT
      },
      footerItem: {
        totalP: 0,
        totalK: 0,
        totalpkg: 0,
        totalNettoExt: 0,
        totalBruto: 0,
        totalNetto: data.netto || 0,
        totalGross: 0,
        totalQty: 0,
        totalRp: 0,
        kadar: data.kadar,
      },
    };
    console.log(JSON.stringify(isi));
    var hasil = JSON.parse(localStorage.getItem("SaldoAwalStok")) || [];
    hasil.push(isi);
    localStorage.setItem("SaldoAwalStok", JSON.stringify(hasil));
    berhasil("Data Tersimpan");
    this.props.dispatch(getListDataSaldoAwalStok());
  }
  render() {
    if (this.state.loading === true) {
      return (
        <div
          className="d-flex no-block justify-content-center align-items-center position-relative"
          style={{ width: "100%", height: "90vh" }}
        >
          <div>
            <Loader type="Oval" color="#00BFFF" height={70} width={70} />
          </div>
        </div>
      );
    } else {
      return (
        <div className="container-fluid">
          <ol className="breadcrumb mb-2">
            <Link to="/saldoawal">
              <button type="button" className="btn btn-secondary">
                <i className="fa fa-angle-left mr-3" aria-hidden="true"></i>{" "}
                Back
              </button>
            </Link>
          </ol>
          <div className="row">
            <div className="col-lg-12">
              <div className="card">
                <HeaderCard title="Saldo Awal Piutang Customer" />
                <div className="card-body">
                  <div className="col-lg-12 mt-3">
                    <div className="row">
                      <div className="col-lg-4">
                        <label htmlFor="">Tanggal</label>
                        <input
                          type="date"
                          className="form-control"
                          onChange={this.handleChange("tanggal")}
                        />
                      </div>
                      <div className="col-lg-4">
                        <label htmlFor="">Nama Lokasi</label>
                        <select
                          className="form-control"
                          onChange={this.handleChange("sales")}
                        >
                          <option value="DEFAULT" key="DEFAULT">
                            SILAHKAN PILIh
                          </option>
                          {this.state.listSales.map((list) => (
                            <option
                              value={list.kodeLokasi}
                              key={list.kodeLokasi}
                            >
                              {list.namaLokasi}
                            </option>
                          ))}
                        </select>
                      </div>
                      <div className="col-lg-12 mt-3">
                        <div className="row">
                          <div className="col-lg-6">
                            <div className="text-left">
                              <button
                                className="btn btn-primary"
                                onClick={() => this.sendSaldoAwal()}
                              >
                                Simpan Saldo Awal
                              </button>
                            </div>
                          </div>
                          <div className="col-lg-6">
                            <div className="text-right">
                              <button
                                className="btn btn-primary"
                                data-target="#tambahBarang"
                                data-toggle="modal"
                              >
                                Tambah Data
                              </button>
                            </div>
                          </div>
                          <div className="col-lg-12 mt-3">
                            {this.props.dataSaldoAwalStok ? (
                              <ToolkitProvider
                                keyField="id"
                                data={this.props.dataSaldoAwalStok}
                                columns={this.state.columns}
                                search
                              >
                                {(props) => (
                                  <div className="form-group">
                                    <div className="text-right mt-5 mb-3">
                                      <SearchBar
                                        {...props.searchProps}
                                        className="form-control"
                                        placeholder="Pencarian"
                                        style={{ margin: 0, top: "50%" }}
                                      />
                                    </div>
                                    <BootstrapTable
                                      {...props.baseProps}
                                      striped
                                      hover
                                      condensed
                                      bordered={true}
                                      headerClasses="header-table"
                                      pagination={paginationFactory()}
                                    />
                                  </div>
                                )}
                              </ToolkitProvider>
                            ) : (
                              <div>
                                {this.props.error ? (
                                  <div className="text-center">
                                    <img
                                      src={notrespond}
                                      style={{ width: "30%" }}
                                      alt="NOT RESPOND"
                                    />
                                    <br />
                                    <h4>Request API Gagal, Silahkan Refresh</h4>
                                  </div>
                                ) : (
                                  <Skeleton width={"100%"} height={180} />
                                )}
                              </div>
                            )}
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div
            id="tambahBarang"
            className="modal fade"
            tabIndex="-1"
            role="dialog"
            aria-labelledby="primary-header-modalLabel"
            aria-hidden="true"
          >
            <div className="modal-dialog modal-lg">
              <div className="modal-content">
                <div className="modal-header modal-colored-header bg-primary">
                  <h4 className="modal-title" id="primary-header-modalLabel">
                    Tambah Data Transaksi
                  </h4>
                  <button
                    id="dismiss_modal1"
                    name="dismiss_modal1"
                    type="button"
                    className="close"
                    data-dismiss="modal"
                    aria-hidden="true"
                  >
                    Ã—
                  </button>
                </div>
                <div className="modal-body">
                  <FormSaldoAwalStok
                    onSubmit={(data) => this.handlerSimpan(data)}
                    tipe={this.state.tipeTransksi}
                  />
                </div>
                <div className="modal-footer">
                  <button
                    type="button"
                    className="btn btn-light"
                    data-dismiss="modal"
                    id="close_modal"
                  >
                    Close
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      );
    }
  }
}

SaldoAwalStok = reduxForm({
  form: "SaldoAwalStok",
  enableReinitialize: true,
})(SaldoAwalStok);
export default connect(maptostate, null)(SaldoAwalStok);
