import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { gagal } from "../../Alert";
import validasiSaldoAwalPiutang from "../../Validations/validasiSaldoAwalPiutang";
import { renderField } from "../../renderField";
import { AxiosMasterGet } from "../../Axios";

class FormSaldoAwalStok extends Component {
  constructor(props) {
    super(props);
    this.state = {
      listSupplier: [],
    };
  }

  componentDidMount() {
    AxiosMasterGet("subcategorys2?kodeKelompok=PERHIASAN")
      .then((res) =>
        this.setState({
          listSupplier: res.data,
        })
      )
      .catch(() =>
        gagal("Sepertinya koneksi bermasalah, mohon periksa koneksi anda")
      );
  }
  setKodeBarang(data) {
    var barang = data.target.value || "DEFAULT|DEFAULT|DEFAULT";
    var split = barang.split("|");
    this.props.change("kadar", split[2]);
  }
  render() {
    return (
      <form onSubmit={this.props.handleSubmit}>
        <div className="row">
          <div className="col-lg-3">
            <label htmlFor="">Kode Barang</label>
            <Field
              name="namaToko"
              component="select"
              className="form-control"
              onChange={(data) => this.setKodeBarang(data)}
            >
              <option value="DEFAULT" key="DEFAULT">
                SILAHKAN PILIH
              </option>
              {this.state.listSupplier.map((list) => (
                <option
                  value={
                    list.kodeJenis +
                    "|" +
                    list.namaJenis +
                    "|" +
                    list.kadarCetak
                  }
                  key={list.kodeJenis + "|" + list.namaJenis}
                >
                  {list.namaJenis}
                </option>
              ))}
            </Field>
          </div>
          <div className="col-lg-3">
            <Field
              label="Kadar (%)"
              name="kadar"
              component={renderField}
              type="text"
            />
          </div>
          <div className="col-lg-3">
            <Field
              label="Netto(Gr)"
              name="netto"
              component={renderField}
              type="text"
            />
          </div>
        </div>
        <div className="col-lg-12">
          <div className="text-right">
            <button className="btn btn-primary" type="submit">
              Simpan
            </button>
          </div>
        </div>
      </form>
    );
  }
}

FormSaldoAwalStok = reduxForm({
  form: "FormSaldoAwalStok",
  enableReinitialize: true,
  validate: validasiSaldoAwalPiutang,
})(FormSaldoAwalStok);
export default connect()(FormSaldoAwalStok);
