const validasiSaldoAwalPiutang = (value) => {
    const errors = {}
    if (!value.namaToko) {
        errors.namaToko = "Tidak Boleh kosong"
    }
    if (!value.detail) {
        errors.detail = "Tidak Boleh kosong"
    }
    if (!value.tglFaktur) {
        errors.tglFaktur = "Tidak Boleh kosong"
    }
    if (!value.noBon) {
        errors.noBon = "Tidak Boleh kosong"
    }
    if (!value.bruto) {
        errors.bruto = "Tidak Boleh kosong"
    }
    if (!value.netto) {
        errors.netto = "Tidak Boleh kosong"
    }
    if (!value.totalSaldoGr) {
        errors.totalSaldoGr = "Tidak Boleh kosong"
    }
    if (!value.totalSaldoRp) {
        errors.totalSaldoRp = "Tidak Boleh kosong"
    }
    return errors
}

export default validasiSaldoAwalPiutang;